.user {
    width: 90%;
    margin: 0 auto;
    .login_header {
        width: 100%;
        height: 1rem;
        box-sizing: border-box;
        padding: 0.25rem 0;
        i {
            font-size: 0.4rem;
            font-weight: bold;
        }
    }
    .login_txt {
        margin: 0.2rem 0 0.5rem;
        color: #555;
        h2 {
            font-size: 0.54rem;
            font-weight: bold;
        }
        p {
            margin-top: 0.2rem;
            line-height: 0.5rem;
        }
    }
    .login_inp {
        input {
            width: 100%;
            line-height: 0.8rem;
            font-size: 0.3rem;
            border: none;
        }
        button {
            width: 100%;
            height: 1rem;
            color: #fff;
            font-size: 0.28rem;
            background: #008489;
            border: none;
            border-radius: 5px;
            margin-top: 0.5rem;
        }
        p{
            font-size: 0.28rem;
            color: #000;
            line-height: 0.5rem;
            margin-top: 0.5rem;
        }
        input {
            border-bottom: 2px solid #008489;
        }
    }
}